@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-stretch: normal;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/opensans/v26/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsjZ0C4n.ttf) format('truetype');
}
@font-face {
  font-family: 'futura-futuris';
  src: url('../fonts/FuturaFuturis/FuturaFuturisC.woff?eo4p0s#iefix') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'futura-futuris';
  src: url('../fonts/FuturaFuturis/FuturaFuturisLightC.woff?eo4p0s#iefix') format('woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'futura-futuris';
  src: url('../fonts/FuturaFuturis/FuturaFuturisC-Bold.woff?eo4p0s#iefix') format('woff');
  font-weight: 700;
  font-style: normal;
}
body {
  font-family: 'futura-futuris', sans-serif;
  font-size: 18px;
  line-height: 1;
  font-weight: 400;
  color: #fff;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
}
p,
li {
  line-height: 1.2;
}
li {
  line-height: 24px;
}
p:last-child,
li:last-child {
  margin-bottom: 0;
}
.header {
  padding: 20px 0 0;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  margin: auto;
  z-index: 2;
}
.header__wrap {
  display: grid;
  grid-template-columns: 323px auto 188px;
  position: relative;
}
.header .logos {
  align-self: center;
  display: flex;
  align-items: flex-start;
}
.header .logos .logo_big {
  max-width: 148px;
}
.header .logos .logo_small {
  max-width: 102px;
  margin-left: 6px;
  margin-top: 12px;
}
.header .nav {
  align-self: center;
  margin-left: auto;
  margin-top: -18px;
  margin-right: 25px;
}
.header .nav__item {
  color: #6fbcbc;
  font-size: 17px;
  font-family: 'futura-futuris', sans-serif;
  font-weight: 400;
  border: 2px solid #fff;
  border-radius: 100px;
  padding: 8px 15px;
}
.header .nav__item:not(:first-of-type) {
  margin-left: 4px;
}
.header .phone {
  align-self: center;
  font-family: 'futura-futuris', sans-serif;
  color: #6fbcbc;
  font-weight: 700;
  font-size: 25px;
  margin-top: -18px;
}
.header .burger {
  position: absolute;
  display: none;
}
.intro {
  position: relative;
  height: 670px;
}
.intro__image {
  position: absolute;
  height: 100%;
  width: 100%;
}
.intro__image .bg {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.intro__image .dots {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro__image .light {
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
.intro__image .light-block {
  position: absolute;
  left: -700px;
  top: 0;
  width: 1400px;
  height: 500px;
  border-radius: 0 30% 70% 0;
  background: radial-gradient(circle farthest-corner at 50% 20%, rgba(111, 188, 188, 0.5) 0, transparent 50%, transparent 60%);
}
.intro__content {
  position: relative;
  max-width: 560px;
  padding-top: 190px;
}
.intro__content .title {
  font-size: 100px;
  font-family: 'futura-futuris', sans-serif;
  font-weight: 700;
}
.intro__content .subtitle {
  font-family: 'futura-futuris', sans-serif;
  font-weight: 400;
  font-size: 35px;
  margin: 30px 0 74px;
  line-height: 1.2;
}
.intro__content .intro__btn {
  font-size: 25px;
  font-family: 'futura-futuris', sans-serif;
  color: #6fbcbc;
  font-weight: 400;
  border: 3px solid #fff;
  padding: 11px 75px 11px 20px;
  border-radius: 100px;
}
.info {
  padding: 55px 0;
}
.info__wrap {
  display: grid;
  grid-template-columns: minmax(min-content, 46%) minmax(min-content, 46%);
  justify-content: space-between;
}
.info__item p {
  font-size: 20px;
  line-height: 1.5;
}
.info__item a {
  text-decoration: underline;
}
.product {
  padding: 70px 0;
}
.product__wrap {
  display: grid;
  grid-template-columns: minmax(min-content, 30%) minmax(min-content, 70%);
}
.product__title {
  align-self: start;
  font-size: 50px;
  font-weight: 400;
  color: #6fbcbc;
  margin: 128px 0 57px;
  line-height: 1.1;
}
.product__booklet {
  align-self: start;
  margin-bottom: 51px;
}
.product__btn {
  align-self: start;
  font-size: 17px;
  color: #6fbcbc;
  border: 3px solid #fff;
  border-radius: 100px;
  padding: 7px 15px;
}
.product__content {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding-top: 59px;
  padding-left: 15px;
}
.product__item {
  width: 230px;
  height: 200px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product__item .hexagon {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
}
.product__item .hexagon polygon {
  fill: transparent;
  stroke-width: 4;
  stroke: #6fbcbc;
  transition: 0.3s;
}
.product__item .icon {
  fill: #6fbcbc;
  transition: 0.3s;
}
.product__item_empty {
  opacity: 0.2;
  position: absolute;
}
.product__item_empty .hexagon polygon {
  fill: none;
}
.product__item:not(.product__item_empty):nth-of-type(1) {
  transform: translateY(219px);
}
.product__item:not(.product__item_empty):nth-of-type(2) {
  transform: translateY(110px) translateX(-41px);
}
.product__item:not(.product__item_empty):nth-of-type(3) {
  transform: translateY(0) translateX(-81px);
}
.product__item:not(.product__item_empty):nth-of-type(4) {
  transform: translateY(239px) translateX(0px);
}
.product__item:not(.product__item_empty):nth-of-type(5) {
  transform: translateY(129px) translateX(-41px);
}
.product__item:not(.product__item_empty):nth-of-type(6) {
  transform: translateY(18px) translateX(-81px);
}
.product__item:not(.product__item_empty):nth-of-type(7) {
  margin-left: auto;
  transform: translateY(-71px) translateX(2px);
}
.product__item:not(.product__item_empty):hover .hexagon polygon {
  fill: #6fbcbc;
}
.product__item:not(.product__item_empty):hover .icon {
  fill: #fff;
}
